<script>
    import { draw } from "svelte/transition";

    export let path;
    export let title;
    export let fillColour = "#333";
    export let strokeColour = "#000";
    export let strokeWidth = "1px";

    let transitionEnded = false;
</script>

<style>
    .path {
        transition: fill 0.3s ease;
    }
    .path:hover {
        fill: #363636;
    }
</style>

<path
on:click
in:draw={{ duration: 3000 }}
on:introend={() => (transitionEnded = true)}
class="path"
d="{path}"
fill={transitionEnded ? fillColour : "white" }
stroke={transitionEnded ? strokeColour : "#202020"}
style="stroke-width:{strokeWidth}">
    <title>{ title }</title>
</path>
