<script>
    import { onMount } from "svelte"

    let windowHeight = 10;
  
    let svg;
    let width = "0";
    let height = "0";

    const resizeSVG = () => {
        let svgBoundingBox = svg.getBBox();

        width = svgBoundingBox.x + svgBoundingBox.width + svgBoundingBox.x;
        height = svgBoundingBox.y + svgBoundingBox.height + svgBoundingBox.y;
    };

    onMount(resizeSVG);
</script>

<style>
  svg {
    margin-top: -5rem;
    margin-right: -1rem;
    transform: rotate(3.5deg);
  }
</style>

<svelte:window on:resize={resizeSVG} bind:innerHeight={windowHeight} />

<svg viewBox="0 0 {width} {height}" height={window.innerHeight - 5} bind:this={svg} >
    <g class="regions">
      <slot />
    </g>
  </svg>